import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const apiSlice = createApi({
  reducerPath: "api",
  baseQuery: fetchBaseQuery({
    baseUrl: "/api/v2",
    credentials: "include",
  }),
  tagTypes: ["Appointment", "Users"],
  endpoints: (builder) => ({
    getAllUsers: builder.query({
      query: () => `/users`,
      providesTags: ["Users"],
    }),
    register: builder.mutation({
      query: (data) => ({
        url: `/register`,
        method: "POST",
        body: data,
      }),
    }),
    login: builder.mutation({
      query: (data) => ({
        url: `/login`,
        method: "POST",
        body: data,
      }),
    }),
    logout: builder.mutation({
      query: () => ({
        url: `/logout`,
        method: "POST",
      }),
    }),

    deassignUser: builder.mutation({
      query: ({ userId, appointmentId }) => ({
        url: `/users/assign/${userId}/${appointmentId}`,
        method: "DELETE",
      }),
      invalidatesTags: ["Appointment", "Users"],
    }),
    assignUser: builder.mutation({
      query: ({ userId, appointmentId }) => ({
        url: `/users/assign/${userId}/${appointmentId}`,
        method: "POST",
      }),
      invalidatesTags: ["Appointment", "Users"],
    }),

    getAppointments: builder.query({
      query: ({ owner = "", status = "", visa = "" } = {}) =>
        `/appointments?owner=${owner}&status=${status}&visa=${visa}`,
      providesTags: ["Appointment"],
    }),
    getAppointment: builder.query({
      query: (id) => `/appointments/${id}`,
      providesTags: ["Appointment"],
    }),
    createAppointment: builder.mutation({
      query: (data) => ({
        url: `/appointments/`,
        method: "POST",
        body: data,
      }),
      invalidatesTags: ["Appointment"],
    }),
    deleteAppointment: builder.mutation({
      query: (id) => ({
        url: `/appointments/${id}`,
        method: "DELETE",
      }),
      invalidatesTags: ["Appointment"],
    }),
    updateAppointment: builder.mutation({
      query: ({ id, data }) => ({
        url: `/appointments/${id}`,
        method: "PATCH",
        body: data,
      }),
      invalidatesTags: ["Appointment"],
    }),

    updateApplicant: builder.mutation({
      query: ({ id, data }) => ({
        url: `/applicants/${id}`,
        method: "PATCH",
        body: data,
      }),
      invalidatesTags: ["Appointment"],
    }),
    deleteApplicant: builder.mutation({
      query: (id) => ({
        url: `/applicants/${id}`,
        method: "DELETE",
      }),
      invalidatesTags: ["Appointment"],
    }),
  }),
});

export const {
  useLoginMutation,
  useRegisterMutation,
  useLogoutMutation,
  useAssignUserMutation,
  useDeassignUserMutation,
  useCreateAppointmentMutation,
  useDeleteApplicantMutation,
  useDeleteAppointmentMutation,
  useGetAppointmentQuery,
  useGetAppointmentsQuery,
  useUpdateApplicantMutation,
  useUpdateAppointmentMutation,
  useGetAllUsersQuery,
} = apiSlice;
