import { Spinner, Stack } from "@chakra-ui/react";
import React from "react";

function CustomSpinner() {
  return (
    <Stack alignItems="center">
      <Spinner
        margin={10}
        thickness="4px"
        speed="0.65s"
        emptyColor="gray.200"
        color="blue.500"
        size="xl"
      />
    </Stack>
  );
}

export default CustomSpinner;
