import React, { lazy, Suspense } from "react";
import {
  createBrowserRouter,
  redirect,
  RouterProvider,
} from "react-router-dom";
import Navbar from "./components/navbar/Navbar";
import ThemeSuspenese from "./theme/ThemeSuspense";
import routes from "./routes";
import Cookies from "js-cookie";
import NotFound from "./pages/404";

const Register = lazy(() => import("./pages/Register"));
const Login = lazy(() => import("./pages/Login"));

const navRoutes = routes.map((route) => {
  return {
    path: route.path,
    element: (
      <Suspense fallback={<ThemeSuspenese />}>
        <Navbar />
        <route.component />
      </Suspense>
    ),
    loader: ({ request }) => {
      const path = new URL(request.url).pathname;
      // user authorize
      const userInfo = Cookies.get("userInfo")
        ? JSON.parse(Cookies.get("userInfo"))
        : null;
      if (!userInfo?.email) return redirect("/login");

      // user context redirect
      console.log(
        userInfo?.role,
        "hasVisited",
        sessionStorage.getItem("hasVisited")
      );
      const hasVisited = sessionStorage.getItem("hasVisited");
      if (!hasVisited) {
        sessionStorage.setItem("hasVisited", true);
        return redirect(userInfo?.role === "admin" ? "/admin" : "/dashboard");
      }
      
      if (userInfo?.role === "user") {
        return path === "/" || path === "/admin"
          ? redirect("/dashboard")
          : null;
      }
      return null;
    },
  };
});

const router = createBrowserRouter([
  ...navRoutes,
  {
    path: "/team/auth/register",
    element: <Register />,
  },
  {
    path: "/login",
    element: <Login />,
  },
  {
    path: "*",
    element: <NotFound />,
  },
]);

function App() {
  return <RouterProvider router={router} />;
}

export default App;
